import React, { HTMLAttributes, ReactNode } from 'react';
import cn from 'classnames';

import defaultColors from './default.module.css';
import styles from './index.module.css';
import invertedColors from './inverted.module.css';

const colorStyles = {
    default: defaultColors,
    inverted: invertedColors,
};

export type FormControlProps = HTMLAttributes<HTMLDivElement> & {
    /**
     * –†–∞—Å—Ç—è–≥–∏–≤–∞–µ—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –Ω–∞ —à–∏—Ä–∏–Ω—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞
     */
    block?: boolean;

    /**
     * –†–∞–∑–º–µ—Ä –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
     */
    size?: 's' | 'm' | 'l' | 'xl';

    /**
     * –ù–∞–±–æ—Ä —Ü–≤–µ—Ç–æ–≤ –¥–ª—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
     */
    colors?: 'default' | 'inverted';

    /**
     * –ó–∞–±–ª–æ–∫–∏—Ä–æ–≤–∞–Ω–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
     */
    disabled?: boolean;

    /**
     * C–æ—Å—Ç–æ—è–Ω–∏–µ —Ç–æ–ª—å–∫–æ –¥–ª—è —á—Ç–µ–Ω–∏—è
     */
    readOnly?: boolean;

    /**
     * –ó–∞–ø–æ–ª–Ω–µ–Ω–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ
     */
    filled?: boolean;

    /**
     * –í—ã–±—Ä–∞–Ω–Ω–æ–µ (—Ñ–æ–∫—É—Å) —Å–æ—Å—Ç–æ—è–Ω–∏–µ
     */
    focused?: boolean;

    /**
     * –û—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ –æ—à–∏–±–∫–∏
     */
    error?: ReactNode | boolean;

    /**
     * –¢–µ–∫—Å—Ç –ø–æ–¥—Å–∫–∞–∑–∫–∏
     */
    hint?: ReactNode;

    /**
     * –õ–µ–π–±–ª –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
     */
    label?: ReactNode;

    /**
     * –í–∏–¥ –ª–µ–π–±–ª–∞ –≤–Ω—É—Ç—Ä–∏ / —Å–Ω–∞—Ä—É–∂–∏
     */
    labelView?: 'inner' | 'outer';

    /**
     * –°–ª–æ—Ç —Å–ª–µ–≤–∞
     */
    leftAddons?: ReactNode;

    /**
     * –°–ª–æ—Ç —Å–ø—Ä–∞–≤–∞
     */
    rightAddons?: ReactNode;

    /**
     * –°–ª–æ—Ç –ø–æ–¥ –ø–æ–ª–µ–º
     */
    bottomAddons?: ReactNode;

    /**
     * –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π –∫–ª–∞—Å—Å
     */
    className?: string;

    /**
     * –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π –∫–ª–∞—Å—Å –¥–ª—è –ø–æ–ª—è
     */
    fieldClassName?: string;

    /**
     * –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π –∫–ª–∞—Å—Å –¥–ª—è –ø–æ–ª—è
     */
    inputWrapperClassName?: string;

    /**
     * –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π –∫–ª–∞—Å—Å –¥–ª—è –ª–µ–π–±–ª–∞
     */
    labelClassName?: string;

    /**
     * –î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–π –∫–ª–∞—Å—Å –¥–ª—è –∞–¥–¥–æ–Ω–æ–≤
     */
    addonsClassName?: string;

    /**
     * –ò–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä –¥–ª—è —Å–∏—Å—Ç–µ–º –∞–≤—Ç–æ–º–∞—Ç–∏–∑–∏—Ä–æ–≤–∞–Ω–Ω–æ–≥–æ —Ç–µ—Å—Ç–∏—Ä–æ–≤–∞–Ω–∏—è
     */
    dataTestId?: string;

    /**
     * –ö–æ–º–ø–æ–Ω–µ–Ω—Ç –ø–æ–ª—è (–∏–Ω–ø—É—Ç, textarea –∏ –ø—Ä.)
     */
    children?: ReactNode;
};

export const FormControl = React.forwardRef<HTMLDivElement, FormControlProps>(
    (
        {
            block = false,
            size = 's',
            colors = 'default',
            className,
            fieldClassName,
            inputWrapperClassName,
            labelClassName,
            addonsClassName,
            disabled,
            readOnly,
            focused,
            filled,
            error,
            hint,
            label,
            labelView = 'inner',
            leftAddons,
            rightAddons,
            bottomAddons,
            children,
            dataTestId,
            ...restProps
        },
        ref,
    ) => {
        const errorMessage = typeof error === 'boolean' ? '' : error;

        return (
            <div
                data-test-id={dataTestId}
                className={cn(
                    styles.component,
                    colorStyles[colors].component,
                    className,
                    styles[size],
                    {
                        [styles.block]: block,
                        [styles.hasLeftAddons]: leftAddons,
                        [styles.hasRightAddons]: rightAddons || error,
                    },
                )}
            >
                {label && labelView === 'outer' && (
                    <span className={cn(styles.above, colorStyles[colors].label)}>{label}</span>
                )}
                <div
                    {...restProps}
                    className={cn(fieldClassName, styles.inner, colorStyles[colors].inner, {
                        [styles.disabled]: disabled || readOnly,
                        [colorStyles[colors].disabled]: disabled || readOnly,
                        [styles.filled]: filled,
                        [colorStyles[colors].filled]: filled,
                        [styles.hasInnerLabel]: label && labelView === 'inner',
                        [styles.focused]: focused,
                        [colorStyles[colors].focused]: focused,
                        [styles.hasError]: error,
                        [colorStyles[colors].hasError]: error,
                    })}
                    ref={ref}
                >
                    {leftAddons && (
                        <div className={cn(styles.addons, styles.leftAddons, addonsClassName)}>
                            {leftAddons}
                        </div>
                    )}

                    <div className={cn(styles.inputWrapper, inputWrapperClassName)}>
                        {label && labelView === 'inner' && (
                            <React.Fragment>
                                <span className={styles.hiddenLabel} aria-hidden={true}>
                                    {label}
                                </span>
                                <div
                                    className={cn(
                                        styles.label,
                                        colorStyles[colors].label,
                                        labelClassName,
                                    )}
                                >
                                    <span className={styles.labelInner}>{label}</span>
                                </div>
                            </React.Fragment>
                        )}

                        <div className={styles.input}>{children}</div>
                    </div>

                    {rightAddons && (
                        <div className={cn(styles.addons, styles.rightAddons, addonsClassName)}>
                            {rightAddons}
                        </div>
                    )}
                </div>
                {bottomAddons}

                {errorMessage && (
                    <span
                        className={cn(styles.sub, styles.error, colorStyles[colors].error)}
                        role='alert'
                    >
                        {errorMessage}
                    </span>
                )}

                {hint && !errorMessage && (
                    <span className={cn(styles.sub, colorStyles[colors].hint)}>{hint}</span>
                )}
            </div>
        );
    },
);
